<template>
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input
            v-model="filters.name"
            placeholder="用户名称"
            @keydown.enter.native="getOrders"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="getOrders">查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addDialog = true">新增</el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!--列表-->
    <el-table :data="orders" highlight-current-row style="width: 100%">
      <el-table-column type="selection" width="55"> </el-table-column>

      <el-table-column prop="name" label="费率字段名" sortable>
      </el-table-column>
      <el-table-column prop="fee" label="费率" sortable> </el-table-column>

        <el-table-column  label="关联渠道" sortable> 
          <template scope="scope">
            <span v-for="item in scope.row.channeleffectivetime_set" :key="item.id">
              {{item.chanel_id.chanel}}
            </span>
          </template>
        </el-table-column>


      <el-table-column label="操作" width="150" fixed="right">
        <template scope="scope">
          <!-- <el-button size="small" @click="handleEdit(scope.$index, scope.row)">查看详情</el-button>-->
          <el-button  @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button
          >

          <el-button
            type="danger"
            @click="delOrder(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size="pageSize"
        :total="total"
        style="float: right"
      >
      </el-pagination>
    </el-col>

    <!--新增dialog  -->
    <el-dialog title="新增用户" :visible.sync="addDialog">
      <el-form class="demo-form-inline" :model="addUserinfo" label-width="100px">
        <el-form-item label="费率字段名">
          <el-input
            v-model="addUserinfo.name"
            auto-complete="off"
          ></el-input>
        </el-form-item>

        <el-form-item label="费率">
         
          <el-input-number v-model="addUserinfo.fee" :precision="2" :step="0.1" :max="10" controls-position="right"></el-input-number>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialog = false">取 消</el-button>
        <el-button type="primary" @click="addUser()">提交</el-button>
      </div>
    </el-dialog>

    <!-- 新增dialog 结束 -->

    <!--编辑dialog  -->
    <el-dialog title="编辑供应商" :visible.sync="editDialog">
      <el-form
        class="demo-form-inline"
        :model="editUserinfo"
        label-width="180px"
        :inline="true"
      >
        <el-form-item label="费率字段名">
          <el-input
            v-model="editUserinfo.name"
            auto-complete="off"
          ></el-input>
        </el-form-item>
<el-form-item label="费率">
       <el-input-number v-model="editUserinfo.fee" :precision="2" :step="0.1" :max="10" controls-position="right"></el-input-number>

</el-form-item>

      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="editDialog = false">取 消</el-button>
        <el-button type="primary" @click="editUser()">提交</el-button>
      </div>
    </el-dialog>

    <!-- 编辑dialog 结束 -->
  </section>
</template>


<script>
import { accMul } from "../../util/calculate.js";
export default {
  data() {
    return {
      filters: {},
      orders: [],
      pageNum: 1,
      pageSize: 100,
      total: 0,
      addUserinfo: {
        quantity: 0,
        unit_price: 0,
      },
      addDialog: false,
      editDialog: false,
      editUserinfo: {},
    };
  },
  created() {
    this.getOrders();
  },
  methods: {
    handleChange() {
      // console.log(this.addUserinfo.quantity)
      // console.log(this.addUserinfo.unit_price)
      // console.log(this.addUserinfo.quantity * this.addUserinfo.unit_price)
      this.addUserinfo.amount = accMul(
        this.addUserinfo.quantity,
        this.addUserinfo.unit_price
      );
    },
    handleChange_() {
      this.editUserinfo.amount = accMul(
        this.editUserinfo.quantity,
        this.editUserinfo.unit_price
      );
    },
    handleCurrentChange(val) {
      this.page = val;
      this.getOrders();
    },
    async getOrders() {
      const data = await this.$axios("FeeField/", {
        params: {
          search: this.filters.name,
          page: this.page,
          page_size: this.pageSize
        },
      });

      console.log(data);

      this.orders = data.data;
      this.total = data.count;
    },
    handleEdit(index, row) {
      this.editDialog = true;
      this.editUserinfo = Object.assign({}, row);

      console.log(this.editUserinfo)
    },
    async delOrder(index, row) {
      let res = await this.$axios.delete(`FeeField/${row.id}`);
      console.log(res);
      this.getOrders();
    },
    async addUser() {
      let res = await this.$axios.post("FeeField/", this.addUserinfo);
      //console.log(res)
      this.addDialog = false;
      this.getOrders();
    },
    async editUser() {
      let res = await this.$axios.put(
        `FeeField/${this.editUserinfo.id}/`,
        this.editUserinfo
      );
      console.log(res);
      this.editDialog = false;
      this.getOrders();
    },
  },
};
</script>

<style>
</style>


